
.emailcapaignPreview{
    width: 100%;
    overflow-y: auto;
    height: calc(100vh - 170px);
}

.email_campaign_view{
    height: calc(100vh - 460px);
    min-height: 280px;
    overflow-y: auto;
}

.emailcapaignPreview #emailEmbedCode_{
    word-break: break-all;
    max-height: 300px;
    overflow: auto;
}

.email_gallery .social_icon{
    display: none;
}

@media (min-width: 992px) {
    .emailcapaigncontainer {
        height: calc(100vh - 249px);
        overflow: auto;
        min-height: 465px;
    }
}
@media (min-width: 1280px) {
    .emailcapaignPreview{
        width: 440px;
        // height: calc(100vh - 190px);
        // overflow: auto;
        // min-height: 520px;
    }
    .emailcapaignPreviewWrap {
        height: calc(100vh - 250px);
        overflow: auto;
    }
    .emailcapaigncontainer {
        height: auto;
    }
    .email_gallery .social_icon{
        display: block;
    }
}


